.about {
  &__title {
    padding-bottom: 32px;

    text-align: start;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }
  
  &__text {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
  }
}
